"use client";
import { usePathname } from 'next/navigation';
import Image from "next/image";
import Link from "next/link";

const products = [
  {
    id: 1,
    name: "T-shirts with multiple colors, for men",
    price: "$10.30",
    image: "/tshirt.png",
    description: "Comfortable t-shirts available in multiple colors.",
  },
  {
    id: 2,
    name: "Jeans shorts for men blue color",
    price: "$10.30",
    image: "/jeans-shorts.png",
    description: "Stylish jeans shorts perfect for summer.",
  },
  {
    id: 3,
    name: "Brown winter coat medium size",
    price: "$12.50",
    image: "/winter-coat.png",
    description: "Warm and stylish winter coat.",
  },
  {
    id: 4,
    name: "Jeans bag for travel for men",
    price: "$34.00",
    image: "/jeans-bag.png",
    description: "Durable travel bag made of denim.",
  },
  {
    id: 5,
    name: "Leather wallet",
    price: "$99.00",
    image: "/leather-wallet.png",
    description: "Premium quality leather wallet.",
  },
  {
    id: 6,
    name: "Canon camera black, 100x zoom",
    price: "$9.99",
    image: "/camera.png",
    description: "High-performance camera with 100x zoom.",
  },
  {
    id: 7,
    name: "Headset for gaming with mic",
    price: "$8.99",
    image: "/headset.png",
    description: "Gaming headset with clear audio and mic.",
  },
  {
    id: 8,
    name: "Smartwatch silver color modern",
    price: "$10.30",
    image: "/smartwatch.png",
    description: "Feature-packed modern smartwatch.",
  },
  {
    id: 9,
    name: "Blue wallet for men leather material",
    price: "$10.30",
    image: "/blue-wallet.png",
    description: "Stylish blue leather wallet for men.",
  },
  {
    id: 10,
    name: "Electric kettle",
    price: "$80.95",
    image: "/kettle.png",
    description: "Efficient and stylish electric kettle.",
  },
];

const ProductDetails = () => {
  // Use usePathname to retrieve the path or query parameters
  const pathname = usePathname();
  const id = pathname.split('/').pop(); // Extract the product ID from the URL
  
  // Find the product by ID
  const product = products.find((item) => item.id === parseInt(id));

  if (!product) {
    return <div>Loading...</div>;
  }

  return (
    <div className="container mx-auto p-6">
      <Link href="/" className="text-blue-500 mb-4 inline-block">
        &larr; Back to Products
      </Link>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {/* Product Image */}
        <Image
          src={product.image}
          alt={product.name}
          width={500}
          height={400}
          className="rounded-lg object-cover"
        />

        {/* Product Details */}
        <div>
          <h1 className="text-2xl font-bold mb-2">{product.name}</h1>
          <p className="text-gray-600 mb-4">{product.description}</p>
          <p className="text-lg font-semibold mb-4">{product.price}</p>

          {/* Dropdown for Sizes */}
          {product.sizes && (
            <div className="mb-4">
              <label htmlFor="size" className="block text-sm font-medium">
                Select Size:
              </label>
              <select
                id="size"
                className="border rounded-lg w-full p-2 mt-1"
              >
                {product.sizes.map((size) => (
                  <option key={size} value={size}>
                    {size}
                  </option>
                ))}
              </select>
            </div>
          )}

          {/* Add to Cart Button */}
          <button className="bg-blue-500 text-white py-2 px-4 rounded hover:bg-blue-600">
            Add to Cart
          </button>
        </div>
      </div>

      {/* Customer Reviews or Related Products Section */}
      <div className="mt-8">
        <h2 className="text-xl font-bold mb-4">Related Products</h2>
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
          {products
            .filter((item) => item.id !== product.id)
            .slice(0, 4) // Show only 4 related products
            .map((relatedProduct) => (
              <Link
                href={`/product/${relatedProduct.id}`}
                key={relatedProduct.id}
              >
                <div className="border rounded-lg p-4 hover:shadow-lg">
                  <Image
                    src={relatedProduct.image}
                    alt={relatedProduct.name}
                    width={200}
                    height={150}
                    className="h-40 w-full object-cover mb-4 rounded-lg"
                  />
                  <h3 className="text-lg font-medium">{relatedProduct.name}</h3>
                  <p className="text-gray-600">{relatedProduct.price}</p>
                </div>
              </Link>
            ))}
        </div>
      </div>
    </div>
  );
};

export default ProductDetails;
